
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap');




.container {
  display: flex;
  align-items: center;
  height: 5.0625em;
  margin-right: 1.875em;
  justify-content: flex-end;
}
.profile {
  background-color: #acffe6;
  border-radius: 50%;
  display: flex;
  margin-left: 1.375em;
  align-items: center;
  justify-content: center;
  height: 2.5em;
  width: 2.5em;
  cursor:pointer;
}

.profile img{
  width:100%;
  height:100%;
  border-radius: 50%;
}

.topbar {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  height: 40px;
}

.search {
  width: 390px;
  height: 32px;
  margin-right: 1em;
  background: rgba(255, 255, 255, 0.4);
  border: 0.8px solid rgba(153, 153, 153, 0.2);
  box-sizing: border-box;
  border-radius: 3px;
}

.search::placeholder {
  font-family: Lato;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 15px;
  color: #999999;
  padding-left: 0.2em;
}

/* =========================================== */
/* THE SECTION OF THE TOP BAR MODAL */

.topbarModal {
  background: #fff;
  position: fixed;
  top: 11%;
  right: 1%;
  width: 100%;
  max-width:300px;
  z-index: 9999;
  border: 1px solid #bebebe;
  border-radius: 8px;
  padding: 20px 0;
}



.backgrounds{
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.158);
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index:99999;
}

/* The section one */
.sectionOne {
  display: flex;
  align-items: center;
  width:85%;
  margin:auto;
  margin-bottom: 17px;
}

.oneLeft {
  border-radius: 50%;
  height: 40px;
  width: 40px;
}

.oneLeft img {
  width: 100%;
  height: 100%;
  border-radius:50%;
}

.oneRight {
  margin-left: 15px;
  line-height: 10px;
}

.oneRight h4 {
  font-size: 13px;
  font-family: Lato;
  font-style: normal;
  font-weight: 900;
  line-height: 20px;
  color: #242424;
}

.online{
  display:flex;
  align-items: center;
}


.circle{
  color:green;
  font-size:7px;
  margin-right:5px;
}

.oneRight p {
  font-size: 12px;
  font-family: Lato;
  font-style: normal;
  font-weight: normal;
  color: #3a3a3a;
}



/* The section two */
.sectionTwo {
  background: #fafafa;
  padding: 2px 10px;
  border-radius: 3px;
  border: 0.5px solid #bebebe;
  width:85%;
  margin:auto;
  cursor: pointer;
  height:30px;
}

.sectionTwo:hover{
  border:1px solid red;
}

/* The section of the picker */

.smileys{
  padding-top:50px;
}

.picker{
  position:fixed;
  top: 20%;
  left:50%;
  /* transform: translate(-80%); */
  height:400px;
  background:#fff;
  width:100%;
  width:300px;
  z-index:999999;
  padding:10px;
}


/* responsiveness for picker */
@media screen and (max-width:650px){
  .picker{
    left:30%;
  }
}


.times{
  color:red;
  position:absolute;
  right:10px;
  top:10px;
  cursor:pointer;
}


/* The section three */
.sectionThree {
  line-height: 25px;
  font-style: normal;
  font-weight: normal;
  margin:auto;
  margin-top:10px;
}

.sectionThree p{
  font-family:Lato;
  color:#242424;
  font-size: 14px;
  font-weight:700;
  cursor:pointer;
  padding:2px 20px;
}

.sectionThree p:hover{
  background:#1692F1;
  color:#fff;
}

.pause{
  display:flex;
  justify-content: space-between;
  align-items: center;
  cursor:pointer;
}

.pause:hover{
  background:#1692F1;
  color:#fff;
}

.pause:hover p{
  background:#1692F1;
  color:#fff;
}

.chevron{
  font-size:10px;
  font-weight:normal;
  margin-right:20px;
}


/* The section four */

.sectionFour {
  line-height: 22px;
  font-style: normal;
  font-weight: normal;
  margin:auto;
  margin-top:-8px;
}

.sectionFour p{
  font-family:Lato;
  color:#242424;
  font-size: 14px;
  font-weight:700;
  cursor:pointer;
  padding:2px 20px;
}

.sectionFour p:hover{
  background:#1692F1;
  color:#fff;
}


/* The section five */

.sectionFive {
  line-height: 22px;
  font-style: normal;
  font-weight: normal;
  margin:auto;
  margin-top:-8px;
}

.sectionFive p{
  font-family:Lato;
  color:#242424;
  font-size: 14px;
  font-weight:700;
  cursor:pointer;
  padding:2px 20px;
}

.sectionFive p:hover{
  background:#1692F1;
  color:#fff;
}